.card {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__name {
  color: #060b35;
  height: 36px;
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  height: 14px;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card .rating {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin: 0 0 24px;
  display: flex;
}

.card .price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin: 0 0 16px;
  display: flex;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border-style: none;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card .rating__reviews {
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card .price__text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card .price__meaning {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  justify-content: space-between;
  align-items: center;
  width: 96px;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:last-child {
  background-image: url("star.822d561e.svg");
}

body {
  color: #000;
  margin: 0;
  font-family: Roboto, sans-serif;
}
